import React, { Component } from 'react'
import Barchart from '../barchart'
import JsonData from '../../data/school/学校视角-招生情况-外省生源数量及比例.json'
export default class Waishengshengyuan extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        外省生源数量及比例
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData} color={1}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}