import React, { Component } from 'react'

import Zaixiaoshengrenshu from './Zaixiaoshengrenshu'

export default class Qichedianzi extends Component {
    render() {
        let { url } = this.props;
        return (
            <div style={ {display: (url === 'qichedianzi' || url === 'all')?'block':'none'} }>
                <Zaixiaoshengrenshu></Zaixiaoshengrenshu>
            </div>
        )
    }
}

