import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';
import echarts from 'echarts';

class Barchart extends Component {
    state = {
        myChart:null,
        chartId:`FloorSpace${new Date().getTime()}-${Math.random()}`
    };

    componentDidMount() {
        let chartId=this.state.chartId
        let chart = echarts.init(document.getElementById(chartId));  
        this.setState({
            myChart:chart
        },()=>{
            this.setChart()
        })      
    }

    setChart = () => {
        let data = this.props.data.RECORDS;
        if (!data || data.length <= 0) {
            return;
        }

        let xAxisData = data.map(item => item.annual);
        let yAxisData1 = data.map(item => item.paperBookNum);
        let yAxisData2 = data.map(item => item.ebookNum);

        var option = {
            tooltip: {
                trigger: 'item',
                formatter: "{a} <br/>{b}: {c} "
            },
            grid: {
                left: 20,
                top: 40,
                bottom: 10,
                right: 20,
                containLabel: true
            },
            legend: {
                icon: 'circle',
                itemWidth: 8,
                itemHeight: 8,
                itemGap: 10,
                textStyle: {
                    fontSize: 12,
                    color: '#fff'
                }
            },
            xAxis: {
                type: 'category',
                data: xAxisData,
                axisLine: {
                    lineStyle: {
                        color: "#00CFFF",
                        type: "dashed"
                    }
                },
                axisLabel: {
                    color: "#00CFFF",
                    interval: 0
                },
                axisTick: {
                    alignWithLabel: true
                }
            },
            yAxis: {
                type: 'value',
                axisLine: {
                    show: false
                },
                axisLabel: {
                    show: false
                },
                splitLine: {
                    show: false
                },
            },
            series: [
                {
                    name: 'paperBook',
                    type: 'bar',
                    barMaxWidth: '20px',
                    itemStyle:{
                        normal:{
                            color:new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                offset: 0,
                                color: 'rgba(0,207,255,1)'
                            }, {
                                offset: 0.8,
                                color: 'rgba(0,207,255,0.8)'
                            }, {
                                offset: 1,
                                color: 'rgba(0,207,255,0.19)'
                            }], false)
                        }
                    },
                    label: {
                        show: true,
                        position: "top",
                        color: "#fff",
                        fontSize: 14
                    },
                    data: yAxisData1
                },
                {
                    name: 'ebook',
                    type: 'bar',
                    barMaxWidth: '20px',
                    itemStyle:{
                        normal:{
                            color:new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                                offset: 0,
                                color: 'rgba(189,16,224,1)'
                            }, {
                                offset: 0.8,
                                color: 'rgba(189,16,224,0.8)'
                            }, {
                                offset: 1,
                                color: 'rgba(189,16,224,0.19)'
                            }], false)
                        }
                    },
                    label: {
                        show: true,
                        position: "top",
                        color: "#fff",
                        fontSize: 14
                    },
                    data: yAxisData2
                }
            ]
        };

        this.state.myChart.setOption(option);
    };

    render() {

        return (
            <div id={this.state.chartId} style={{width: '100%', height: '100%'}}></div>
        );
    }
}

export default withRouter(Barchart);