import React, { Component } from 'react'
import Barchart from '../../barchart'
import JsonData from '../../../data/major/shukong/入职半年满意度（%）.json'
export default class Ruzhibannian extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        入职半年满意度（%）
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}