import React, { Component } from 'react'
import Barchart from '../../barchart'
import JsonData from '../../../data/major/qichedianzi/在校生人数.json'
export default class Zaixiaoshengrenshu extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        在校生人数
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}