import React, { Component } from 'react'
import Barchart from '../barchart/luqufenshuxian'
import JsonData from '../../data/school/学校视角-招生情况-录取分数线.json'
export default class Luqufenshuxian extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        录取分数线
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}