
import React, { Component } from 'react'
import Barchart from '../barchart'
import JsonData from '../../data/school/学生文艺体育获奖（市级、省级、国家级）.json'
export default class XueShenWenYiTiYu extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        学生文艺体育获奖
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}