import React, { Component } from 'react'

import Ruzhibannian from './Ruzhibannian'

export default class Shukong extends Component {
    render() {
        let { url } = this.props;
        return (
            <div style={ {display: (url === 'shukong' || url === 'all')?'block':'none'} }>
                <Ruzhibannian></Ruzhibannian>
            </div>
        )
    }
}

