import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Carousel} from 'antd'
import { carouselTitleAction } from './../../redux/actions/carouselTitleAction';
import { majorMenuAction } from './../../redux/actions/majorMenuAction'
import { isShowMajorMenuAction } from './../../redux/actions/isShowMajorMenuAction'

// 学校
import Shengjunjiangzhu from './component/school/Shengjunjiangzhu'
import Xuexiaojingfei from './component/school/Xuexiaojingfei'
import Xuexiaojingfeizhehe from './component/school/Xuexiaojingfeizhehe'
import Shuangdaitou from './component/school/Shuangdaitou'
import Renyuanpeixun from './component/school/Renyuanpeixun'
import Jieshouliuxuesheng from './component/school/Jieshouliuxuesheng'
import Kaifajiaoxuebiaozhun from './component/school/Kaifajiaoxuebiaozhun'
import Jianzhumianji from './component/school/Jianzhumianji'
import Tushuzongliang from './component/school/Tushuzongliang'
import Biyesheji from './component/school/Biyesheji'
import Biyeshengzongshu from './component/school/Biyeshengzongshu'
import Shehuipeixun from './component/school/Shehuipeixun'
import Laifangjiaoliu from './component/school/Laifangjiaoliu'
import Boshixuewei from './component/school/Boshixuewei'
import Fudaoyuanshuliang from './component/school/Fudaoyuanshuliang'
import Shixunjidi from './component/school/Shixunjidi'
import Dingdanzhanbi from './component/school/Dingdanzhanbi'
import Jiaoshishijian from './component/school/Jiaoshishijian'
import Hezuoqiyeshuliang from './component/school/Hezuoqiyeshuliang'
import Hezuoqiyetouru from './component/school/Hezuoqiyetouru'
import Zaixiaojianshe from './component/school/Zaixiaojianshe'
import Jishuchengguoshu from './component/school/Jishuchengguoshu'
import Xueshengchuangxin from './component/school/Xueshengchuangxin'
import Yingyongjishu from './component/school/Yingyongjishu'
import Zhuanyeshuzhanbi from './component/school/Zhuanyeshuzhanbi'
import Guojializhi from './component/school/Guojializhi'
import Shetuanrongyu from './component/school/Shetuanrongyu'
import Shetuanshuliang from './component/school/Shetuanshuliang'
import Xueshengdangyuan from './component/school/Xueshengdangyuan'
import Baodaolv from './component/school/Baodaolv'
import Luqufenshuxian from './component/school/Luqufenshuxian'
import Waishengshengyuan from './component/school/Waishengshengyuan'
import Zhaoshengshuliang from './component/school/Zhaoshengshuliang'
import Baimingxuesheng from './component/school/Baimingxuesheng'
import Jiaoxuekeyan from './component/school/Jiaoxuekeyan'
import Shengjunjiaoxue from './component/school/Shengjunjiaoxue'
import Shengjuntushu from './component/school/Shengjuntushu'
import Zichan from './component/school/Zichan'
import CanJiaGongYiHuoDong from './component/school/CanJiaGongYiHuoDong'
import XueShenChuangXinChuangYe from './component/school/XueShenChuangXinChuangYe'
import XueShenWenYiTiYu from './component/school/XueShenWenYiTiYu'
import ZhuanYeShiJianXueShi from './component/school/ZhuanYeShiJianXueShi'

//专业
import Major from './component/major'

class Xianghao extends Component {
  render() {
    let autoplaySpeed = 40000;
      const settings = {
          // autoplay: true,
          autoplaySpeed,
          dots: true,
          infinite: false,
          slidesToShow: 1,
          slidesToScroll: 1,
          vertical: true,
          verticalSwiping: true,
          beforeChange: (currentSlide, nextSlide) => {
              this.props.changeCarouselTitle(nextSlide);
              if (nextSlide === 1){
                  this.props.filterMajor("1");
                  this.props.toggleMajorMenu(true);
              }else {
                  this.props.toggleMajorMenu(false);
              }
          },
          afterChange: (currentSlide) => {
              if (currentSlide === 4) {
                  setTimeout(() => {
                      if (this.slider && this.slider.goTo) {
                          this.slider.goTo(0)
                      }
                  }, autoplaySpeed)
              }
          },
      };
    return (
      <Carousel  ref={slider => (this.slider = slider)} {...settings} >
        <div>
          <div style={{ height:'calc(100vh - 74px)',overflowY:'auto'}}>          
            <Shengjunjiangzhu></Shengjunjiangzhu>
            <Xuexiaojingfei></Xuexiaojingfei>
            <Xuexiaojingfeizhehe></Xuexiaojingfeizhehe>
            <Shuangdaitou></Shuangdaitou>
            <Renyuanpeixun></Renyuanpeixun>
            <Jieshouliuxuesheng></Jieshouliuxuesheng>
            <Kaifajiaoxuebiaozhun></Kaifajiaoxuebiaozhun>
            <Jianzhumianji></Jianzhumianji>
            <Tushuzongliang></Tushuzongliang>
            <Biyesheji></Biyesheji>
            <Biyeshengzongshu></Biyeshengzongshu>
            <Shehuipeixun></Shehuipeixun>
            <Laifangjiaoliu></Laifangjiaoliu>
            <Boshixuewei></Boshixuewei>
            <Fudaoyuanshuliang></Fudaoyuanshuliang>
            <Shixunjidi></Shixunjidi>
            <Dingdanzhanbi></Dingdanzhanbi>
            <Jiaoshishijian></Jiaoshishijian>
            <Hezuoqiyeshuliang></Hezuoqiyeshuliang>
            <Hezuoqiyetouru></Hezuoqiyetouru>
            <Zaixiaojianshe></Zaixiaojianshe>
            <Jishuchengguoshu></Jishuchengguoshu>
            <Xueshengchuangxin></Xueshengchuangxin>
            <Yingyongjishu></Yingyongjishu>
            <Zhuanyeshuzhanbi></Zhuanyeshuzhanbi>
            <Guojializhi></Guojializhi>
            <Shetuanrongyu></Shetuanrongyu>
            <Shetuanshuliang></Shetuanshuliang>
            <Xueshengdangyuan></Xueshengdangyuan>
            <Baodaolv></Baodaolv>
            <Luqufenshuxian></Luqufenshuxian>
            <Waishengshengyuan></Waishengshengyuan>
            <Zhaoshengshuliang></Zhaoshengshuliang>
            <Baimingxuesheng></Baimingxuesheng>
            <Jiaoxuekeyan></Jiaoxuekeyan>
            <Shengjunjiaoxue></Shengjunjiaoxue>
            <Shengjuntushu></Shengjuntushu>
            <Zichan></Zichan>
            <CanJiaGongYiHuoDong></CanJiaGongYiHuoDong>
            <XueShenChuangXinChuangYe></XueShenChuangXinChuangYe>
            <XueShenWenYiTiYu></XueShenWenYiTiYu>
            <ZhuanYeShiJianXueShi></ZhuanYeShiJianXueShi>
          </div>
        </div>

        {/* 每一页的划分(专业维度-向好) */}
        {/* <div>
          <div style={{ height:'calc(100vh - 74px)',overflowY:'auto'}}>
            <Major></Major>
          </div>
        </div> */}
        
      </Carousel>
    )
  }
}

export default connect(null, 
  (dispatch) => {
      return {
          changeCarouselTitle(type){
              const action = carouselTitleAction(type);
              dispatch(action);
          },
          filterMajor(type){
              const action = majorMenuAction(type);
              dispatch(action);
          },
          toggleMajorMenu(bool){
              const action = isShowMajorMenuAction(bool);
              dispatch(action);
          }
      }
  })(Xianghao)
