import React, { Component } from 'react'
import Barchart from '../barchart'
import JsonData from '../../data/school/学校视角-就业情况-毕业生总数及分布.json'
export default class Biyeshengzongshu extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        毕业生总数及分布
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}