import React, { Component } from 'react'
import Barchart from '../barchart'
import JsonData from '../../data/school/学校视角-师资队伍-博士学位人数占专任教师占比.json'
export default class Boshixuewei extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        博士学位人数占专任教师占比
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData} color={1}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}