
import React, { Component } from 'react'
import Barchart from '../barchart'
import JsonData from '../../data/school/专业实践学时占专业总学时比例（%）.json'
export default class ZhuanYeShiJianXueShi extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        专业实践学时占专业总学时比例
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData} color={1}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}