import React, { Component } from 'react'
import Barchart from '../barchart'
import JsonData from '../../data/school/学校视角-校企合作-合作企业投入（捐赠）情况.json'
export default class Hezuoqiyetouru extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        合作企业投入（捐赠）情况
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData} color={1}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}