import React, { Component } from 'react'

import Hezuoqiyeshuliang from './Hezuoqiyeshuliang'

export default class Kuaiji extends Component {
    render() {
        let { url } = this.props;
        return (
            <div style={ {display: (url === 'kuaiji' || url === 'all')?'block':'none'} }>
                <Hezuoqiyeshuliang></Hezuoqiyeshuliang>
            </div>
        )
    }
}

