import React, { Component } from 'react'
import Barchart from '../barchart/tushuzongliang'
import JsonData from '../../data/school/学校视角-基本情况-图书总量（纸质数字资源TB）.json'
export default class Tushuzongliang extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        图书总量（万册）
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}