import React, { Component } from 'react'
import Barchart from '../barchart/shixunjidi'
import JsonData from '../../data/school/学校视角-实训基地（校内校外）.json'
export default class Shixunjidi extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        实训基地（校内校外）
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}