import React, { Component } from 'react'
import Barchart from '../../barchart'
import JsonData from '../../../data/major/jisuanji/就业率（%）.json'
export default class Jiuyelv extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        就业率（%）
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}