
import React, { Component } from 'react'
import Barchart from '../barchart'
import JsonData from '../../data/school/参加公益活动学生人次.json'
export default class CanJiaGongYiHuoDong extends Component {
    render() {
        return (
            <div className='item-wrapper'>
                <div className='item-box'>
                    <div className='item-title'>
                        <span>
                        参加公益活动学生人次
                                    </span>

                    </div>
                    <div className='item-chart'>
                    <Barchart data={JsonData}></Barchart>
                    </div>
                </div>
            </div>
        )
    }
}