import React, { Component } from 'react'

import Gongweishu from './Gongweishu'

export default class Qicheyunyong extends Component {
    render() {
        let { url } = this.props;
        return (
            <div style={ {display: (url === 'qicheyunyong' || url === 'all')?'block':'none'} }>
                <Gongweishu></Gongweishu>
            </div>
        )
    }
}

